/* ........modal-window.................... */
.hide {
z-index: -100;
}


.modal_back {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.64);
  position: absolute;
  top: 0;
  left: 0;
  font-size: 20px;
  z-index: 100;
}
.modal_window {
  width: 500px;
  max-width: 100%;
  background-color: #f4f5f7;
  border-radius: 2px;
  display: flex;
  justify-content: space-between;
}
.window__data__txt-name{
  padding-left: 15px;
  padding-top: 15px;
}

.window__data__name {
  margin: 10px 0 15px 15px;
  background: transparent;
  border-radius: 3px;
  box-shadow: none;
  font-size: 16px;
  /* height: 32px; */
  line-height: 24px;
  min-height: 24px;
  max-height: 240px;
  resize: none;
  
}

.window__data__description {
  padding-left: 15px;
}
.description {
  background: transparent;
  border-radius: 3px;
  box-shadow: none;
  font-size: 15px;
  line-height: 24px;
  resize: none;
  min-width: 215px;
  min-height: 100px;
  outline: none;
  border: 1px solid #999;
  margin-bottom: 10px;
}
.description::placeholder{
  font-family: 'Times New Roman';
  font-size: 15px;
 }

.close_modal-window {
  font-size: 30px;
  cursor: pointer;
}

.ic-close {
  padding: 15px;
  cursor: pointer;
}

.window__data__save-description {
  margin: 15px 0 15px 15px;
  text-decoration: none;
  cursor: pointer;
}
.window__data__description-txt {
  margin: 0 0 15px 15px;
}
.window__data__save-description{
  background-color: #6b778c;
  box-shadow: none;
  border: none;
  color: #000;
  font-family: "Times New Roman";
  line-height: 20px;
  font-weight: 400;
  box-sizing: border-box;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  cursor: pointer;
  padding: 6px 12px;
  margin: 0 6px 10px  15px;
  font-size: 15px;
}

.window__data__save-description:hover{
  background-color: #4c6186;
}
.new-card__description {
  overflow: auto;
}